body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
  /* Navbar component */
.navbar {
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 55px;
  background: #A9C2E7;
  z-index: 1;
}

.navbar a {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  font-family: 'Space Mono';
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 55px;
  text-align: center;
  color: #000000;
  text-decoration: none;
}

.navbar a:hover {
  border-bottom: 3px solid #000000;
}

  
  /* Footer component */
  
  footer {
    font-family: space mono;
    position: relative;
    height: 70px;
    left: 0;
    bottom: 0;
    width: 100%;
    background-clip: content-box;
    padding-top: 100px;
    background-color: #A9C2E7;
  }
  
  footer img {
    height: 50px;
    width: 50px;
    margin-right: 50px;
    float: right;
  }
  